:root {
  --white: #fff;
  --black: #212429;
  --black-transparent-1: rgba(0, 0, 0, 0.1);
  --black-transparent-2: rgba(0, 0, 0, 0.2);
  --primary: #4264eb;
  --primary-lighten: #b3c4ea;
  --primary-darken: #2443c2;
  --error: #F03D3E;
  --success: #374FC7;
  --grey-1: #F8F9FA;
  --grey-2: #edede8;
  --grey-3: #DDE2E5;
  --grey-4: #ACB5BD;
  --grey-5: #495057;
  --text-primary: var(--black);
  --text-secondary: var(--grey-4);
  --active-text-color: var(--text-primary);
  --white-bg: var(--white);
  --input-text-color: var(--grey-5);
}
.vr-toggle {
  position: relative;
  display: inline-block;
  white-space: nowrap;
  line-height: 1;
  width: max-content;
}
.vr-toggle input {
  position: absolute;
  left: 0;
  top: 0;
  min-width: 52px;
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 0;
  margin: 0;
  padding: 0;
  cursor: pointer;
  overflow: visible;
}
.vr-toggle input,
.vr-toggle__state label {
  min-width: 52px;
}
.vr-toggle__state {
  line-height: 28px;
  position: relative;
}
.vr-toggle__state label {
  position: initial;
  display: inline-block;
  font-weight: 400;
  margin: 0;
  min-width: 52px;
  line-height: 1.2;
  font-size: 16px;
  color: var(--text-primary);
  text-indent: 64px;
  text-transform: unset;
}
.vr-toggle__state label:after,
.vr-toggle__state label:before {
  content: "";
  width: 28px;
  height: 28px;
  display: block;
  box-sizing: border-box;
  border-radius: 0;
  border: 1px solid transparent;
  z-index: 0;
  position: absolute;
  left: 0;
  top: 0;
  background-color: transparent;
}
.vr-toggle__state label:before {
  border: 1px solid var(--grey-3);
}
.vr-toggle__state:before {
  content: "";
  border: 1px solid var(--grey-3);
  border-radius: 16px;
  width: 52px;
  box-sizing: border-box;
  height: 28px;
  position: absolute;
  top: 0;
  z-index: 0;
  transition: all 0.5s ease;
}
.vr-toggle .vr-toggle__state label:after,
.vr-toggle .vr-toggle__state label:before {
  transition: left 0.5s ease;
  border-radius: 100%;
  left: 0;
  border-color: transparent;
  transform: scale(0.8);
}
.vr-toggle .vr-toggle__state label:after {
  background-color: var(--grey-3) !important;
}
.vr-toggle input:checked ~ .vr-toggle__state:before {
  background-color: var(--primary);
  border-color: var(--primary);
}
.vr-toggle input:checked ~ .vr-toggle__state label:before {
  opacity: 0;
}
.vr-toggle input:checked ~ .vr-toggle__state label:after {
  background-color: var(--white) !important;
  left: 24px;
}
input:disabled,
input:disabled + .vr-toggle__state {
  cursor: default;
  pointer-events: none;
}
input:disabled + .vr-toggle__state label {
  opacity: 0.5;
}
:not(.vr-toggle) input:disabled + .vr-toggle__state label:before {
  background-color: var(--grey-3);
}
.vr-toggle input:disabled + .vr-toggle__state label:before,
.vr-toggle input:disabled + .vr-toggle__state label:after {
  background-color: var(--grey-4) !important;
}
.vr-toggle input:disabled + .vr-toggle__state:before {
  background-color: var(--grey-3);
}
label {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.2;
  letter-spacing: 0.64px;
  text-transform: uppercase;
  color: var(--grey-4);
  margin-bottom: 5px;
  display: inline-flex;
}
input {
  padding: 7px 14px;
  border-radius: 8px;
  border: 1px solid var(--grey-3);
  background-color: var(--white-bg);
  box-shadow: 0 1px 1px rgba(51, 154, 240, 0.1);
  color: var(--input-text-color);
  line-height: 1.2;
  outline: none;
}
input::placeholder {
  color: var(--grey-3);
}
input:focus,
input:active {
  border-color: var(--primary);
}
.vr-form-group {
  display: flex;
  flex-direction: column;
}
.vr-form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 20px;
}
.vr-form-devider {
  margin: 0;
  border: none;
  border-bottom: 1px solid var(--black-transparent-1);
}
.vr-container {
  max-width: 800px;
  margin: 80px auto 0;
  width: 100%;
  display: flex;
  gap: 20px;
  flex-direction: column;
}
.vr-header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.vr-header-container__code {
  margin-bottom: 10px;
}
.vr-header-container__header-content {
  font-size: 40px;
  margin-bottom: 30px;
  margin-top: 0;
  text-align: center;
}
.vr-card {
  border-radius: 6px;
  background: var(--white);
  padding: 20px;
  box-shadow: 2px 3px 10px var(--black-transparent-2);
  min-height: 355px;
}
.vr-card--height-auto {
  min-height: unset;
}
.vr-card-header {
  border-bottom: 1px solid var(--black-transparent-1);
  padding: 0;
  padding-bottom: 7px;
  line-height: 1.2;
  white-space: normal;
  text-transform: none;
  letter-spacing: -0.8px;
  margin: 0 0 16px 0;
}
.vr-card-content {
  display: flex;
  gap: 20px;
  flex-direction: column;
}
.vr-button {
  display: inline-flex;
  justify-content: center;
  height: 48px;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  padding: 16px 14px;
  text-align: center;
  border-radius: 8px;
  border: none;
  background: none;
  box-shadow: none;
  outline: none;
  cursor: pointer;
  transition: all 0.2s linear;
  min-width: 120px;
}
.vr-button--primary {
  background: var(--primary);
  color: var(--white);
}
.vr-button:hover {
  cursor: pointer;
  background: var(--primary-darken);
}
.vr-table-container {
  overflow-x: auto;
}
table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}
table td,
table th {
  border: 1px solid var(--grey-3);
  padding: 8px;
  vertical-align: baseline;
}
table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: var(--primary-lighten);
}
table tr td:first-child {
  white-space: nowrap;
}
*::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
*::-webkit-scrollbar-track {
  background-color: #c5c5c536;
}
*::-webkit-scrollbar-thumb {
  background-color: #dadada;
}
.vr-drop-zone {
  background-color: #eaf1ff;
  height: 180px;
  width: 100%;
  cursor: pointer;
  transition: background-color 0.2s ease-out, padding 0.2s ease-out;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 20px;
}
.vr-drop-zone__wrapper {
  border: 1px dashed var(--grey-4);
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.vr-drop-zone__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.vr-drop-zone.vr-highlight-dropzone {
  background-color: #d8e4fc;
  padding: 15px;
}
.vr-drop-zone-icon {
  filter: invert(44%) sepia(84%) saturate(2006%) hue-rotate(211deg) brightness(101%) contrast(95%);
  width: 50px;
}
.github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out;
}
@keyframes octocat-wave {
  0%,
  100% {
    transform: rotate(0);
  }
  20%,
  60% {
    transform: rotate(-25deg);
  }
  40%,
  80% {
    transform: rotate(10deg);
  }
}
@media (max-width: 500px) {
  .github-corner:hover .octo-arm {
    animation: none;
  }
  .github-corner .octo-arm {
    animation: octocat-wave 560ms ease-in-out;
  }
}
body {
  font-family: 'Roboto', sans-serif;
  padding: 30px 20px;
  color: var(--text-primary);
}
code {
  color: var(--primary-darken);
}
pre {
  padding: 6px 12px;
  font-size: 14px;
  color: var(--text-primary);
  background-color: var(--grey-2);
  border-radius: 4px;
  overflow-x: auto;
}
a {
  color: var(--primary);
  text-decoration: none;
}
[role=button],
a,
area,
button,
input:not([type=range]),
label,
textarea {
  touch-action: manipulation;
}
input[type=checkbox] {
  box-sizing: border-box;
  padding: 0;
}
[role=button],
a,
area,
button,
input:not([type=range]),
label,
textarea {
  touch-action: manipulation;
}

.vr-preview {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(var(--vr-preview__auto-repeat__min, 150px), 1fr));
  gap: var(--vr-preview__gap, 15px);
  max-height: var(--vr-preview, 250px);
  overflow-y: auto;
}
.vr-preview-image {
  position: relative;
  overflow: hidden;
}
.vr-preview-image:hover .vr-preview-remove {
  opacity: 1;
}
.vr-preview-image:hover .vr-preview-info {
  bottom: 0;
}
.vr-preview-source {
  width: 100%;
  height: var(--vr-preview-source__height, 100px);
  box-sizing: border-box;
  transition: all 0.2s ease;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: var(--vr-preview-source__background-color, #e5effba8);
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: var(--vr-preview-source__font-weight, 600);
  color: var(--vr-preview-source__color, #a0a0a0);
  user-select: none;
}
.vr-preview-remove {
  opacity: 0;
  width: var(--vr-preview-remove__size, 20px);
  height: var(--vr-preview-remove__size, 20px);
  position: absolute;
  right: 0;
  top: 0;
  font-weight: bold;
  background-color: var(--vr-preview-remove__background, rgba(66, 66, 66, 0.64));
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: opacity 0.22s;
  color: var(--vr-preview-remove__color, #fff);
}
.vr-preview-info {
  position: absolute;
  right: 0;
  bottom: var(--vr-preview-info__bottom, -30px);
  left: 0;
  height: var(--vr-preview-info__height, 24px);
  font-size: var(--vr-preview-info__font-size, 0.8rem);
  background: rgba(66, 66, 66, 0.64);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--vr-preview-info__padding, 0 5px);
  transition: bottom 0.22s;
  color: var(--vr-preview-info__color, #fff);
}

